@font-face {
  font-family: "blackchanceryregular";
  src: url("../fonts/blkchcry-webfont.woff2") format("woff2"), url("../fonts/blkchcry-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
body {
  background: #0c0500;
  font-family: "blackchanceryregular";
}

.wrapper {
  max-width: 1440px;
  margin: 0 auto;
}

header {
  margin: 0 auto;
  padding: 80px 0;
  text-align: center;
  border-bottom: solid 3px #fee780;
  border-image-slice: 1;
  border-image-source: linear-gradient(to left, #dd7646 0%, #fee780 100%);
}
header h1, header p, header a, header span {
  background: linear-gradient(85deg, #dd7646 -50%, #fee780 110%);
  -webkit-text-fill-color: transparent;
  text-decoration: none;
  -webkit-background-clip: text;
}
header h1 {
  font-size: 3.8rem;
  line-height: 4.5rem;
  letter-spacing: 0.3rem;
}
header p {
  padding-top: 60px;
  font-size: 1.9rem;
  line-height: 2.5rem;
}

nav {
  padding: 20px 0;
}
nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
nav ul li {
  padding: 0 10px;
}
nav span {
  font-size: 1.5rem;
}
nav svg {
  width: 22px;
  height: 22px;
  fill: #fee780;
}

.grid {
  margin: 0 auto;
  padding: 70px 0;
  -webkit-box-shadow: 0px 0px 200px -180px #fee780;
  -moz-box-shadow: 0px 0px 200px -180px #fee780;
  box-shadow: 0px 0px 200px -180px #fee780;
}

.grid-sizer,
.grid-item {
  width: 33.333%;
}

.grid-item {
  padding: 20px;
}
.grid-item img {
  border-radius: 0.1rem;
  display: block;
  max-width: 100%;
}
.grid-item img:not(.loaded) {
  visibility: hidden;
}

footer {
  padding: 60px;
  background: linear-gradient(to left, #dd7646 0%, #fee780 100%);
  text-align: center;
  font-size: 1.8rem;
}

@media screen and (max-width: 767px) {
  .grid-sizer,
.grid-item {
    width: 100%;
  }
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .grid-sizer,
.grid-item {
    width: 50%;
  }
}